<template>
    <div class="city-one">
        <!-- 头部 -->
        <Header>
             <router-link to='/' slot="first"><div class="logo">ele.me</div></router-link>
             <div slot="mid"></div>
             <router-link to="/login" slot="last"><div class="login">登录|注册</div></router-link>
        </Header>
        <!-- 定位城市 -->
        <div class="city-div2">
            <p><span>当前定位城市:</span> <span>定位不准时，请在城市列表中选择</span></p >
            <p>
            <span>{{this.list.name}}</span>
            <span class="el-icon-arrow-right"></span>
            </p >
        </div>

        <div class="city-div3"></div>
        <!-- 热门城市 -->
        <div class="city-div4">
            <p>热门城市</p >
            <ul>
                <li v-for="(item,index) in arr" :key="index" @click="dian(item)">{{item.name}}</li>
            </ul>
        </div>

        <div class="city-div3"></div>
        <!-- 字母排序 -->
        <div v-for="(item,index) in arrtow" :key="index" class="city-div4">
            <p>{{item}}(按字母排序)</p >
            <ul>
                <li v-for="(i,index) in array[item]" :key="index" @click="dian(i)">{{i.name}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
import Header from "@/page/City/Header"
import axios from "axios"
export default {
    components:{
        Header
    },
    name: "city-one",
    data(){
        return {
            list:[],
            arr:[],
            array:[],
            arrtow:[]
        }
},
    methods:{
       dian(v){
           this.$router.push(`/city?id=${v.id}&name=${v.name}`)
       }
    },
    mounted(){
            this.axios.get("http://elm.cangdu.org/v1/cities?type=guess").then((res)=>{
            this.list=res.data
            })
            this.axios.get("http://elm.cangdu.org/v1/cities?type=hot").then((s)=>{
            this.arr=s.data
            })
            this.axios.get("http://elm.cangdu.org/v1/cities?type=group").then((s)=>{
            this.arrtow=Object.keys(s.data).sort()
            this.array=s.data
            // console.log(this.array)
            })
    }
    }
</script>

<style lang='scss'>
// *{font-size: 0.2rem;}
a{color: #fff;}
.city-div2,ul,li,p{
    margin: 0;padding: 0;
} 
ul,li{
    list-style: none;
}
.city-div2{
    height: 1.4rem;width: 100%;
    font-size: 0.22rem;margin-top:0.85rem;border-bottom: 0.02rem solid #E4E4E4;
    p:nth-of-type(1){
        height: 50%;font-size: 0.2rem;display: flex;align-items: center;
        justify-content: space-between;color: 6C6C6C;box-sizing: border-box;
        border-bottom:0.01rem solid #E3E3E3;
        span:nth-of-type(2){
            color: gray;
        }
    }
    p:nth-of-type(2){
        height: 50%;font-size: 0.3rem;display: flex;align-items: center;
        justify-content: space-between;
        span:nth-of-type(1){
            color: #3792E5;
        }
    }
}
    .city-div4{
        width: 100%;
        p{
        line-height: 0.54rem;font-size: 0.23rem;
        }
            ul{
            display: flex;flex-wrap: wrap;
                li{
                width: 25%;height: 0.7rem;text-align: center;line-height: 0.7rem;font-size: 0.2rem;
                box-sizing: border-box;color: #3792E5;border: 0.01rem solid #E3E3E3;
                }
            }
    }
</style>



